import React, { Component } from 'react';
import { ICharacterDetails, ICharacterEnergy, getEnergyPercent, isEnergyFull } from '../../models/CharacterDetails';
import { IQuestService } from '../../services/QuestService';
import { IItemDetails } from '../../models/ItemDetails';
import './ReplenishEnergy.scss';

import SwiperCore, { Navigation } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react/swiper-react.js';
import 'swiper/swiper-bundle.min.css';
import 'swiper/swiper.scss';
import 'swiper/modules/navigation/navigation.scss';
import { ItemService } from '../../services/ItemService';

export interface IReplenishEnergyProps {
  questService: IQuestService;
  itemService: ItemService;
  character: ICharacterDetails;
  energy: ICharacterEnergy;
  close: any;
}

export interface IReplenishEnergyState {
  selectedItem: IItemDetails | null;
}

export class ReplenishEnergy extends Component<IReplenishEnergyProps, IReplenishEnergyState> {
  energyItems: IItemDetails[];

  constructor(props: any) {
    super(props);

    this.state = {
      selectedItem: null
    };

    this.selectEnergyItem = this.selectEnergyItem.bind(this);
    this.replenishEnergy = this.replenishEnergy.bind(this);
    this.close = this.close.bind(this);

    this.energyItems = this.props.questService!.questItems.energy.map(
      id => this.props.itemService!.itemMap.get(id.toNumber())!
    );

    SwiperCore.use([Navigation]);
  }

  selectEnergyItem(energyItem: IItemDetails): void {
    this.setState({ selectedItem: energyItem });
  }

  replenishEnergy(energyItem: IItemDetails): void {
    this.props.questService.replenishEnergy(this.props.character, energyItem);
    this.close();
  }

  close() {
    this.props.close();
  }

  render() {
    return (
      <div className="is-active modal noselect">
        <div className="modal-background" onClick={this.close}></div>
        <div id="replenish-energy" className="modal-content replenish-energy">
          <div
            className="card"
            style={{
              backgroundImage: `linear-gradient(rgba(10, 10, 10, 0.6), rgba(10, 10, 10, 1)),url(${
                this.props.character!.image
              })`
            }}>
            <button className="modal-close is-large" aria-label="close" onClick={this.close}></button>
            <div className="is-flex is-justify-content-center replenish-energy-title pb-3">
              {this.props.character.name}
            </div>
            <div className="replenish-energy-content">
              <div className="is-flex is-flex-direction-column is-align-items-center is-justify-content-center mt-1">
                {this.renderEnergy()}
              </div>

              <div
                className="is-flex is-flex-direction-column is-align-items-center is-justify-content-center mt-5"
                style={{ overflow: 'hidden' }}>
                {this.renderItems()}
              </div>

              <div className="is-flex is-flex-direction-column is-align-items-stretch is-justify-content-center character-stats mt-5">
                <button
                  className="button replenish-button is-action"
                  onClick={() => this.replenishEnergy(this.energyItems[0])}
                  disabled={this.energyItems.length <= 0 || isEnergyFull(this.props.energy)}>
                  {isEnergyFull(this.props.energy) && <span>Energy is Full</span>}
                  {!isEnergyFull(this.props.energy) && this.state.selectedItem && (
                    <span>Use {this.state.selectedItem.name}</span>
                  )}
                  {!isEnergyFull(this.props.energy) && !this.state.selectedItem && <span>Select an Item</span>}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  renderEnergy() {
    return (
      <React.Fragment>
        {this.props.energy && (
          <div className="energy-container">
            <div
              className={`energy-bar ${isEnergyFull(this.props.energy) ? 'energy-full' : ''}`}
              style={{
                background: `linear-gradient(to right, var(--red) 0%, var(--green) ${
                  65 + (100 - getEnergyPercent(this.props.energy))
                }%)`,
                width: `${getEnergyPercent(this.props.energy)}%`
              }}>
              Energy
            </div>
          </div>
        )}
      </React.Fragment>
    );
  }

  renderItems() {
    if (!this.props.questService.isItemUseApproved) {
      return (
        <React.Fragment>
          <span className="column meme is-half has-text-centered">
            <h3 className="empty-items py-3">Using Energy Items Not Approved</h3>
          </span>
        </React.Fragment>
      );
    }

    var availableItems = this.energyItems.filter(i => i.balance > 0);

    if (availableItems.length > 0) {
      return (
        <React.Fragment>
          <Swiper
            navigation={{
              prevEl: '.swiper-energy-prev',
              nextEl: '.swiper-energy-next'
            }}
            spaceBetween={20}
            slidesPerView="auto">
            {availableItems.map(i => (
              <SwiperSlide key={i.id}>
                <div
                  className="energy-item"
                  onClick={() => {
                    this.selectEnergyItem(i);
                  }}>
                  <img alt={i.name} className="token" src={i.imageUrl} />
                  <span className="item-name">{i.name}</span>
                  {i.balance > 0 && <span className="item-balance pl-2">{i.balance}</span>}
                  {!i.balance && <span className="item-balance pl-2">-</span>}
                </div>
              </SwiperSlide>
            ))}
            {/* <div className="swiper-button-prev swiper-energy-prev"></div>
            <div className="swiper-button-next swiper-energy-next"></div> */}
          </Swiper>
        </React.Fragment>
      );
    } else {
      return (
        <span className="column meme is-half has-text-centered">
          <h3 className="empty-items py-3">No Energy Items</h3>
        </span>
      );
    }
  }
}
