import { BigNumber } from '@ethersproject/bignumber';
import React, { Component } from 'react';
import { CharacterStat, ICharacterDetails } from '../../models/CharacterDetails';
import { Utils } from '../../services/Utils';
import { IQuestService } from '../../services/QuestService';
import './BeginQuest.scss';

import SwiperCore, { Navigation } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react/swiper-react.js';
import 'swiper/swiper-bundle.min.css';
import 'swiper/swiper.scss';
import 'swiper/modules/navigation/navigation.scss';
import { IItemDetails } from '../../models/ItemDetails';
import { ItemService } from '../../services/ItemService';

export interface IBeginQuestProps {
  questService: IQuestService;
  itemService: ItemService;
  character: ICharacterDetails;
  close: any;
}

export interface IBeginQuestState {
  questLength: number;
  selectedItem: IItemDetails | null;
}

export class BeginQuest extends Component<IBeginQuestProps, IBeginQuestState> {
  equipItems: IItemDetails[];

  constructor(props: any) {
    super(props);

    this.state = {
      questLength: 1,
      selectedItem: null
    };

    this.handleAmountChange = this.handleAmountChange.bind(this);
    this.getCurrentAmount = this.getCurrentAmount.bind(this);
    this.selectEquipItem = this.selectEquipItem.bind(this);
    this.beginQuest = this.beginQuest.bind(this);
    this.getQuestCost = this.getQuestCost.bind(this);
    this.close = this.close.bind(this);

    this.equipItems = this.props.questService!.questItems.equip.map(
      id => this.props.itemService!.itemMap.get(id.toNumber())!
    );

    SwiperCore.use([Navigation]);
  }

  handleAmountChange(amount: number) {
    this.setState({ questLength: this.state.questLength + amount });
  }

  getCurrentAmount(stat: CharacterStat): number {
    return this.props.character.stats.find(s => s.name === CharacterStat[stat])!.value;
  }

  selectEquipItem(energyItem: IItemDetails): void {
    this.setState({ selectedItem: energyItem });
  }

  beginQuest(nft: ICharacterDetails): void {
    if (this.state.questLength) {
      this.props.questService.beginQuest(
        this.props.character,
        this.state.questLength,
        this.state.selectedItem ? [this.state.selectedItem.id] : []
      );
      this.close();
    }
  }

  getQuestCost(): string {
    return Utils.formatUnits(this.props.questService.questPrice?.mul(BigNumber.from(this.state.questLength)));
  }

  close() {
    this.props.close();
  }

  render() {
    return (
      <div className="is-active modal noselect">
        <div className="modal-background" onClick={this.close}></div>
        <div id="begin-quest" className="modal-content begin-quest">
          <div
            className="card"
            style={{
              backgroundImage: `linear-gradient(rgba(10, 10, 10, 0.6), rgba(10, 10, 10, 1)),url(${
                this.props.character!.image
              })`
            }}>
            <button className="modal-close is-large" aria-label="close" onClick={this.close}></button>
            <div className="is-flex is-justify-content-center begin-quest-title pb-3">{this.props.character.name}</div>
            <div className="begin-quest-content">
              <ul>
                {this.renderStatListItem(CharacterStat.Stamina)}
                {this.renderStatListItem(CharacterStat.Strength)}
                {this.renderStatListItem(CharacterStat.Speed)}
                {this.renderStatListItem(CharacterStat.Courage)}
                {this.renderStatListItem(CharacterStat.Intelligence)}
              </ul>

              <div
                className="is-flex is-flex-direction-column is-align-items-center is-justify-content-center mt-4"
                style={{ overflow: 'hidden' }}>
                {this.renderItems()}
              </div>

              <div className="is-flex is-flex-direction-column is-align-items-center is-justify-content-center mt-5">
                <p>
                  <span>Quest Length</span>
                </p>
              </div>

              <div className="is-flex is-flex-direction-column is-align-items-stretch is-justify-content-center character-stats mt-4">
                <div className="field has-addons">
                  <div className="control">
                    <button
                      className="button minus-button is-action"
                      disabled={this.state.questLength <= 1}
                      onClick={() => this.handleAmountChange(-1)}>
                      <span className="icon">
                        <i className="fas fa-minus"></i>
                      </span>
                    </button>
                  </div>
                  <div className="control is-fullwidth">
                    <input className="input is-action" type="text" disabled={true} value={this.state.questLength} />
                  </div>
                  <div className="control">
                    <button
                      className="button plus-button is-action"
                      disabled={this.state.questLength >= this.props.questService.maxNumberOfQuests}
                      onClick={() => this.handleAmountChange(+1)}>
                      <span className="icon">
                        <i className="fas fa-plus"></i>
                      </span>
                    </button>
                  </div>
                </div>
                <button
                  className="button begin-button is-action"
                  onClick={() => this.beginQuest(this.props.character)}
                  disabled={this.state.questLength <= 0}>
                  Begin Quest
                </button>
              </div>

              <div className="is-flex is-flex-direction-column is-align-items-center is-justify-content-center mt-5">
                <p>
                  <span>Cost {this.getQuestCost()}</span>
                  {this.props.questService.questConfig.questPayConfig!.assetConstants!.symbol} (+ gas)
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  renderStatListItem(stat: CharacterStat) {
    return (
      <CharacterStatListItem currentStat={stat} currentValue={this.getCurrentAmount(stat)}></CharacterStatListItem>
    );
  }

  renderItems() {
    if (!this.props.questService.isItemUseApproved) {
      return (
        <React.Fragment>
          <span className="column meme is-half has-text-centered">
            <h3 className="empty-items py-3">Equipping Not Approved</h3>
          </span>
        </React.Fragment>
      );
    }

    var availableItems = this.equipItems.filter(i => i.balance > 0);

    if (availableItems.length > 0) {
      return (
        <React.Fragment>
          <p className="mb-3">
            {this.state.selectedItem && <span>Equipping {this.state.selectedItem.name}</span>}
            {!this.state.selectedItem && <span>Nothing selected to equip</span>}
          </p>
          <Swiper
            navigation={{
              prevEl: '.swiper-equip-prev',
              nextEl: '.swiper-equip-next'
            }}
            spaceBetween={20}
            slidesPerView="auto">
            {availableItems.map(i => (
              <SwiperSlide key={i.id}>
                <div
                  className="equip-item"
                  onClick={() => {
                    this.selectEquipItem(i);
                  }}>
                  <img alt={i.name} className="token" src={i.imageUrl} />
                  <span className="item-name">{i.name}</span>
                  {i.balance > 0 && <span className="item-balance pl-2">{i.balance}</span>}
                  {!i.balance && <span className="item-balance pl-2">-</span>}
                </div>
              </SwiperSlide>
            ))}
            {/* <div className="swiper-button-prev swiper-equip-prev"></div>
            <div className="swiper-button-next swiper-equip-next"></div> */}
          </Swiper>
        </React.Fragment>
      );
    } else {
      return (
        <React.Fragment>
          <span className="column meme is-half has-text-centered">
            <h3 className="empty-items py-3">No Equipable Items</h3>
          </span>
        </React.Fragment>
      );
    }
  }
}

interface ICharacterStatListItemProps {
  currentStat: CharacterStat;
  currentValue: number;
}

class CharacterStatListItem extends Component<ICharacterStatListItemProps> {
  render() {
    return (
      <li className="is-flex is-justify-content-space-between character-stat-list-item">
        <span className="is-flex is-justify-content-space-between" style={{ width: '100%' }}>
          <span>{CharacterStat[this.props.currentStat]}</span>
          <span>{this.props.currentValue}</span>
        </span>
      </li>
    );
  }
}
